button > i , button > span,button > b{
  pointer-events: none;
}
.form_select_block{
  margin-bottom: 0;
}
.custom-file-input:lang(en)~.custom-file-label:after{
  content: '\e923';
}
.tabs_scroll::-webkit-scrollbar-track{
  border-width: 0px 1px;
  border-style: solid;
  border-color: #dee2e6;
}
.tabs_scroll::-webkit-scrollbar-thumb{
  border-width: 0px 0px 0px 1px;
  border-style: solid;
  border-color: #dee2e6;
}
.tabs_scroll::-webkit-scrollbar-track , .tabs_scroll::-webkit-scrollbar-thumb{
  background: #fff;
}
.pagination_area{
  margin-top: 20px;
}
.work_area .th_style{
  background-color: #2e355b;
  border: 0;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.5);
}
.work_area .th_style span{
  color: #fff;
  position: relative;
}
.work_area .th_style span > span.th_sort:before{
  font-family: 'fcs';
  content: '\e90c';
  top: -8px;
  left: 3px;
  position: absolute;
  opacity: .3;
}
.work_area .th_style span > span.th_sort:after{
  font-family: 'fcs';
  content: '\e90a';
  bottom: -8px;
  left: 3px;
  position: absolute;
  opacity: .3;
}
.work_area .th_style span > span.th_sort.sort_asc:before{
  opacity: 1;
  color: #fff;
}
.work_area .th_style span > span.th_sort.sort_desc:after{
  opacity: 1;
  color: #fff;
}
.work_area table tr th:first-child,.work_area table tr td:first-child{
  text-align: left;
}

/* ---------- 這裡以下開始是fcs -> vcast的CSS修正 ---------- */
.work_area .table {
  border: 1px solid #ced4da;
  font-weight: 600;
}
.sys_area .work_area .table .sys_percent{
  width: 40%;  
}
.sys_area .work_area .table .sys_editor{
  width: 8%;  
}
.sys_area .work_area .table .sys_editor b{
  color: #75498d; 
}
.sys_area .work_area .table  tr{
  opacity: .999;
}
.sys_area .work_area .table td.sys_percent{
  padding: 0;
  position: relative;
}
.sys_area .work_area .table .sys_percent > .outer_bar{
  background-color: #cccccc;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
  border: 4px solid white;
  border-left-width: 0;
  border-right-width: 0;
  width: 100%;
}
.sys_area .work_area .table tr:nth-of-type(odd) .sys_percent > .outer_bar{
  border-color: rgb(242, 242, 242);
}
.sys_area .work_area .table .sys_percent > .outer_bar > .inner_bar{
  background-color: #76b656;
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
}
.sys_area .work_area .table .sys_percent > .outer_bar > .inner_bar > span{
  width: 100%;
}
.sys_area .btn_common.float-left + .search_area{
  margin-left: 20px;
}
.sys_area .list-group-item .col-form-label{
  color: #000000;
}
.sys_area .list-group-item .btn_common{
  margin: 5px 0px 10px 0px;
}
.sys_area .list-group-item .btn_common + .btn_common{
  margin-left: 10px;
}
.second_area .work_area table .icon-fiber_manual_record{
  margin-right: 10px;
}
.second_area .work_area table .icon-fiber_manual_record.complete{
  color: #5dd024;
}
.second_area .work_area table .icon-fiber_manual_record.ongoing{
  color: #ff0000;
}
.second_area .work_area table .icon-fiber_manual_record.standby{
  color: #ffed17;
}
.v_hidden{
  visibility: hidden;
}
.second_area .second_left_ul .list-group-item{
  padding: 0.2rem 0.75rem 0 0.75rem;
}
.second_area .second_left_ul .list-group-item.product_item{
  background-color: #f2f2f2;
  padding: 0.75rem;
}
.second_area .second_left_ul .list-group-item.product_item .item_text{
  display: block;
  font-size: 18px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.6;
  letter-spacing: normal;
  text-align: left;
  color: #808080;
}
.second_area .second_left_ul .list-group-item +.list-group-item{
  margin-top: 20px;
}
.second_area .second_right_ul{
  border: 1px solid #c8c5d5;
  padding: 0.75rem;
  background-color: #efefef; 
}
.second_area .second_right_ul .list_group{
  margin-bottom: 0;
}
.second_area .second_right_ul .button_group > span{
  width: auto;
}
.second_area .second_right_ul .button_group > span > .lab_group {
  padding: 0 10px;
}
.second_area .second_right_ul .list-group-item{
  padding: 0;
  margin-top: 10px;
}
.second_area .second_right_ul .list-group-item .card_header{
  padding: 0.5rem 1rem;
  font-family: MicrosoftSansSerif;
  font-size: 16px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.2;
  letter-spacing: 1.6px;
  color: #ffffff;
}
.second_area .second_right_ul .list-group-item .card_header.below15{
  background-color: #59c496;
}
.second_area .second_right_ul .list-group-item .card_header.overOr15{
  background-color: #51beef;
}
.second_area .second_right_ul .list-group-item .card_content{
  padding: 0.5rem 1rem;
  background-color: #ffffff;
}
.second_area .second_right_ul .list-group-item .card_content > span{
  display: block;
  font-size: 14px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.37;
  letter-spacing: 1.4px;
  text-align: left;
  color: #808080;
}
.second_area .fixed_height{
  height: 370px;
  overflow-y: auto;
}
.second_area .sort_area .button_group{
  width: 260px;
}
.sys_area.second_area .form_select_block .form_select_bind{
  width: 100%;
}
.modal_fix input + .col-form-label{
  padding-left: 0.5rem;
}
.modal_fix input{
  display: inline-block;
  width: auto;
}
.modal_fix .form-group.row > .col-form-label{
  text-align: center;
  padding-right: 0;
}
